<template>

  <div class="content">
    <mt-picker :slots="myAddressSlots" valueKey="name" :visibleItemCount="3" @change="addressChange"
               :itemHeight="50"></mt-picker>
  </div>

</template>

<script>

  ////json
  import { zmGetProvincesArr,zmGetCitiesArr,zmGetAreaArr } from '@/assets/zmRegions'

  export default {
    name: "addressPopupTool",
    data() {
      return {
        region: '',//
        province: '',//
        city: '',//
        area: '',//
        provinceCode: '',//
        cityCode: '', //
        areaCode: '',//

        regionInit: false,//picker3

        myAddressSlots: [//
          {
            flex: 1,
            values: zmGetProvincesArr(), //
            className: 'slot1',
            textAlign: 'center'
          },
          //
          {
            divider: true,
            content: '',
            className: 'slot2'
          },
          //
          {
            flex: 1,
            values: zmGetCitiesArr('11'),
            className: 'slot3',
            textAlign: 'center'
          },
          {
            divider: true,
            content: '',
            className: 'slot4'
          },
          //
          {
            flex: 1,
            values: zmGetAreaArr('1101'),
            className: 'slot5',
            textAlign: 'center'
          }
        ],
      }
    },

    methods: {

      addressChange(picker, values) {
        // console.table(picker);
        // console.table(values);
        if (this.regionInit) {
          //
          if (values[0] && values[1]  && values[2]) {

            this.region = values[0]["name"] + values[1]["name"] + values[2]["name"];
            this.province = values[0]["name"];
            this.city = values[1]["name"];
            this.county = values[2]["name"];
            this.provinceCode = values[0]["code"];
            this.cityCode = values[1]["code"];
            this.countyCode = values[2]["code"];

            // console.log(":")
            // console.table(picker.getSlotValues(0));
            // console.log("//:")
            // console.table(picker.getValues());

            //
            picker.setSlotValues(1, zmGetCitiesArr(values[0]["code"]));
            picker.setSlotValues(2, zmGetAreaArr(values[1]["code"]));


            this.$emit("getRegion", this.region)
          }else {
            console.log("");
          }


        } else {
          this.regionInit = true;
        }
      },

    },
  }


</script>

<style scoped>

  div.content {
    text-align: center;
    font-size:.5rem;
  }

  label.title {
    font-size:.5rem;
    margin-left: 10px;
    margin-right: 10px;
    width: 80%;
    background: white;
    font-size: 1rem;
    color: #7f828b;
  }

</style>


